import React, { Component } from 'react'; 
import { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom'; 
import Home from './pages/Home'; 
import './App.css'; 

class App extends Component { 
render() { 
	return ( 
	<Router> 
			<Switch> 
			<Route exact path='/' component={Home}></Route> 
			</Switch> 
	</Router> 
); 
} 
} 



export default App; 
